import '@/assets/css/popup.scss'
import _ from 'lodash'

export default function install( Vue ) {
  let nextKey = 0 //   
  let popupList = [] //   
  let anchorVm = null  //vue create  popup-anchor 

  function updateAnchor( isFullPopup ) {
    if( anchorVm ) {
      anchorVm.$forceUpdate()//popup-anchor  render  .       $forceUpdate      

      document.documentElement.classList.add( 'active-popup' )
      if( isFullPopup ) {
        document.documentElement.classList.add( 'fullpopup-show' )
      }

      anchorVm.$nextTick( function() {
        //       .
        if( popupList.length == 0 ) {
          //          .
          //      .
          document.documentElement.classList.remove( 'fullpopup-show' )
          document.documentElement.classList.remove( 'active-popup' )
        }
        if( isFullPopup ) {
          window.document.documentElement.scrollTop = 0
          window.document.body.scrollTop = 0
        }
      } )
    }
  }

  function getPathWithoutHash() {
    let path = window.location.href
    let idx = window.location.href.indexOf( '#' ) // # 
    if( idx >= 0 ) {
      path = path.substring( 0, idx ) // #   
    }
    return path
  }

  function makePopupKeyListString( pList ) {
    let ks = _.map( pList, p => p.popupKey )
    return _.join( ks, ',' )
  }

  let popupManager = {
    open: function( component, params, isFullPopup ) { //      .
      if( !component ) {
        throw new Error( 'popupError: component is null' )
      }

      let key = '_popup_key_' + (nextKey++) // 
      let resolve
      let promise = new Promise( ( rs, rj ) => {
        resolve = rs
      } )

      popupList.push( { component: component, popupKey: key, params: params, resolve: resolve } ) //popupList open  .
      updateAnchor( isFullPopup )

      window.history.pushState( null, 'popup', getPathWithoutHash() + '#popup=' + makePopupKeyListString( popupList ) )//      

      return { popup_key: key, promise: promise } // key    .
    },
    close: function( inst, retParam ) {
      if( !inst ) {
        return
      }

      for( let i = 0; i < popupList.length; i++ ) {
        if( inst instanceof Vue && inst === popupList[i].componentInstance ||
          inst.popupKey === popupList[i].popupKey ) {
          let resolve = popupList[i].resolve
          popupList.splice( i, 1 )
          updateAnchor()
          window.history.back()
          setTimeout( () => resolve( retParam ), 0 )
          return
        }
      }
    },
    closeAll: function() {
      let removed = _.remove( popupList, () => true )
      updateAnchor()
      setTimeout( () => {
        _.forEach( removed, item => {
          let resolve = item.resolve
          resolve()
        } )
      }, 0 )

      window.history.replaceState( null, undefined, getPathWithoutHash() )
    }
  }

  //   Vue      Vue.prototype $popupManager .
  Object.defineProperty( Vue.prototype, '$popupManager', {
    get() { return popupManager }
  } )

  Vue.component( 'popup-anchor', {
    name: 'popup-anchor',
    created() { //popup-anchor  
      anchorVm = this //popup-anchor   .  open close  $forceUpdate  .
    },
    render: function( createElement ) {
      let children = popupList.map( popup => createElement( //createElement   ,     
        popup.component,
        {
          key: popup.key, // key
          props: popup.params ? popup.params : {}, //open  
          hook: {
            create: function( _, vnode ) { //VNode create  
              popup.componentInstance = vnode.componentInstance //    .
            }
          }
        }
      ) )
      return createElement( 'div', children )
    }
  } )
}



